import { createApp } from 'vue';
import waitUntil from 'async-wait-until';
import ThoughtImplant from './ThoughtImplant.vue';

$(async () => {
  console.info('[]  MVU...');

  //  MVU 
  try {
    await Promise.race([
      waitGlobalInitialized('Mvu'),
      new Promise((_, reject) =>
        setTimeout(() => reject(new Error('MVU 5')), 5000)
      ),
    ]);
    console.info('[] MVU ');
  } catch (err) {
    console.error('[] MVU :', err);
    toastr.error('MVU ');
    return;
  }

  //  stat_data  3 
  // 
  console.info('[]  stat_data ...');
  try {
    await waitUntil(
      () => {
        try {
          const messageId = getCurrentMessageId();
          const vars = Mvu.getMvuData({ type: 'message', message_id: messageId });
          return _.has(vars, 'stat_data');
        } catch {
          return false;
        }
      },
      { timeout: 3000, intervalBetweenAttempts: 100 }
    );
    console.info('[] stat_data ');
  } catch (err) {
    //  return
    // 
    console.warn('[] stat_data :', err);
  }

  console.info('[]  Vue');

  //  Vue 
  const app = createApp(ThoughtImplant);

  //  Vue 
  app.config.errorHandler = (err, _instance, info) => {
    console.error('[] Vue :', err);
    console.error('[] :', info);
  };

  //  #app
  app.mount('#app');

  console.info('[] ');
});
